import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import "rxjs/Rx";
import { CommonService } from "../services/commonService";
import { WorkOrderService } from "../services/workOrderService";
import swal from 'sweetalert2';

declare interface DataTable {
    workOrdHeaderRow: string[];
    workOrdFooterRow: string[];
    workOrdDataRows: any;
}

declare const $: any;

@Component({
    selector: 'app-work-order',
    templateUrl: './work-order.component.html',
    styleUrls: ['./work-order.component.scss'],
    providers: [CommonService, WorkOrderService]
})

export class WorkOrderComponent implements OnInit {
    public workOrdDataTable: DataTable;
    public workOrderList: any;
    public workOrdJobID: any;
    public wOlastSyncDate

    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private workOrderService: WorkOrderService,
        private activatedRoute: ActivatedRoute,
    ) {
    }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {
            this.activatedRoute.queryParams.subscribe((params: Params) => {
                if (params['jobID'] == undefined) {
                    this.router.navigate(['jobs']);
                }
                else {
                    this.workOrdJobID = params['jobID'];
                    this.getWorkOrderDetails(this.workOrdJobID);
                    this.workOrdDataTable = {
                        workOrdHeaderRow: ['WOs', 'Date Due', 'Description', 'Status', 'Assigned', 'Actions'],//'ID'
                        workOrdFooterRow: ['WOs', 'Date Due', 'Description', 'Status', 'Assigned', 'Actions'],
                        workOrdDataRows: this.workOrderList
                    };
                }
            });
        }
        else {
            this.router.navigate(['pages/login']);
        }
    }
    getWorkOrderDetails(jobID: any) {
        $('.loader').show();
        this.workOrderService.GetWorkOrdersByJobId(this.commonService.baseApiUrl, jobID).subscribe(
            data => {
                var dataWorkOrders = JSON.stringify(data);
                var workParse = JSON.parse(dataWorkOrders);
                var workOrderList = JSON.parse(workParse["_body"]);
                localStorage.removeItem("WorkOrdersList");
                this.workOrderList = null;
                if (workOrderList != null) {
                    if (workOrderList.length > 0) {
                        this.wOlastSyncDate = workOrderList[0].WOSyncDate != null ? workOrderList[0].WOSyncDate : "No Sync";
                    }
                    $("#workDatatables").dataTable().fnDestroy();
                    localStorage.setItem("WorkOrdersList", JSON.stringify(workOrderList));
                    this.workOrderList = workOrderList;

                    this.workOrdDataTable = {
                        workOrdHeaderRow: ['WOs', 'Date Due', 'Description', 'Status', 'Assigned', 'Actions'],//'ID'
                        workOrdFooterRow: ['WOs', 'Date Due', 'Description', 'Status', 'Assigned', 'Actions'],
                        workOrdDataRows: this.workOrderList
                    };

                    setTimeout(() => {
                        $("#workDatatables").dataTable().fnDestroy();
                        $('#workDatatables').DataTable({
                            'pagingType': 'full_numbers',
                            'lengthMenu': [[10, 25, 50, -1], [10, 25, 50, 'All']],
                            responsive: true,
                            language: {
                                search: '_INPUT_',
                                searchPlaceholder: 'Search records',
                            }

                        });

                        const tableWO = $('#workDatatables').DataTable();

                        // Edit record
                        tableWO.on('click', '.edit', function () {

                        });

                        // Delete a record
                        tableWO.on('click', '.remove', function (e: any) {
                            const $trWO = $(this).closest('tr');
                            tableWO.row($trWO).remove().draw();
                            e.preventDefault();
                        });

                        // Like record
                        tableWO.on('click', '.like', function () {
                            alert('You clicked on Like button');
                        });

                    }, 10);

                    $('.loader').hide();
                }
            }
        );
    }

    goToWorkAssignment(WorkId: any) {
        this.router.navigate(['job-assignment'], { queryParams: { workID: WorkId } });
    }
}



